body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.data-pane {
  float: left;
  padding: 5px;
  width: 25%;
  border: black;
  border-width: 1px;
}
.content-pane {
  float: none;
  padding: 5px;
  width: 30%;
  border: black;
  border-width: 1px;
}

.wave {
  -webkit-animation: slide 1s linear infinite;
          animation: slide 1s linear infinite;
}

@-webkit-keyframes slide {
  0% {
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@keyframes slide {
  0% {
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}
.metadata-item {
  text-align: left;
  color: #777777;
  font-size: 12px;
}

.metadata-title {
  text-align: center;
  color: #777777;
  font-size: 14px;
}

.slider-pane {
  float: left;
  padding: 5px;
  width: 30%;
  text-align: right;
  color: #777777;
  font-size: 12px;
}

.metadata-item {
  text-align: left;
  color: #777777;
  font-size: 12px;
}

.metadata-title {
  text-align: center;
  color: #777777;
  font-size: 14px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #3333aa;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

